h1 {
  text-align: center;
}

nav{
  margin-top:20px;
}
nav a{
  padding:10px;
}

.hero-left{
  width:50%;
  padding:25px;
  display:flex;
  flex-direction: column;
}

.hero-left p{
  text-align: center;
}

.hero-left button{
  width: 100px;
  margin:10px auto;
}
.hero-right{
  width:50%;
  padding:25px;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.upvote{
  max-width:700px;
  padding:25px;
  margin: 0 auto;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
